.highlighted-content__border {
    border-left: $measure--half solid $color-red50;
    margin-bottom: $measure--basic;
    margin-top: $measure--basic;
    padding-bottom: $measure--basic;
    padding-left: $measure--medium1;
    padding-top: $measure--basic;
}

.highlighted-content__info {
    margin-bottom: $measure--basic;
    margin-top: $measure--basic;
    padding-bottom: $measure--basic;
    padding-left: $measure--medium3;
    padding-top: $measure--basic;
    position: relative;

    &:before {
        @include smalltype();

        color: $color-gray6;
        content: '\f05a';
        font-family: font-family('icon');
        left: $measure--quarter;
        position: absolute;
    }
}

.highlighted-content__info,
.highlighted-content__border {
    p {
        margin: 0;
    }

    p:nth-child(n + 2) {
        display: none;
    }
}
