.wrapper--course--pgt__profiles__heading,
.wrapper--course--pgt__gallery__heading,
.wrapper--course--pgt__student-work__heading {
    @include full-width-heading();
}

.course--pgt__testimonials {
    @include content-grid();
    @include media('<=tablet') {
        @include standard-box();
    }
    .course--pgt__testimonials__heading {
        grid-column: 1/-1;
        @include media('>tablet') {
            grid-column: 2/-1;
        }
    }
    .slider {
        grid-column: 1/-1;
        display: block;
        margin-bottom: $measure--basic;
        @include media('>tablet') {
            grid-column: 2/11;
        }
        li {
            margin-top: 0;
            picture {
                margin-left: 0;
            }
        }
    }
    .unibuddy-card {
        grid-column: 1/-1;
        margin-bottom: $measure--basic;
        @include media('>tablet') {
            grid-column: 12/-2;
        }
        height: fit-content;
        margin-top: $measure--small1;
    }
}
