main {
    .landing {
        margin-top: $measure--small2;
 
        @include media('>=desktop') {
            grid-column: 6 / span 14;
        }
    }
// Desktop specific layout if secondary navigation is present
    .navigation--secondary {
        & + .landing {
            @include media('>=small') {
                grid-column: 7 / span 18;

                .landing--functional__content__wrap {
                    @include content-grid();
            
                    .landing--functional__content{
                        grid-column: 4 / span 12;
                    }  
                }  
            }
        }
    }
}

// Tablet mobile layout
@include media('>mobile', '<small') {
    .landing--functional__content__wrap {
        @include content-grid();
        @include x-column-grid(24);

        .landing--functional__content {
            grid-column: 7 / span 12;
        }
    }     
}

@include media('<small') {
    .landing--functional__finder{
        @include default-margin();
    }  
}

@include media('<mobile') {
    .landing--functional__content {
        //@include default-padding();
        @include standard-box();
    }
}
