.banner-svg {
    background: linear-gradient(to bottom, $color-gray2, $color-purewhite 75%);
}

.banner-svg--wrapper {
    @include standard-grid-wrapper();
}

.banner-svg--wrapper__svg {
    grid-column: 3/17;
    position: relative;

    .hero-image {
        height: 14rem;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
    }
}

.banner-svg--wrapper__title {
    grid-column: 3/17;
    position: relative;
    padding-top: $measure--medium7;
    padding-bottom: $measure--medium7;
    text-align: center;

    h1,
    p {
        @include standard-box();
    }
}

.banner-svg--wrapper__title,
.banner-svg--wrapper__svg {
    @include media('<=mobile') {
        grid-column: 1/-1;
    }

    @include media('>mobile', '<=between') {
        grid-column: 1/13;
    }
}
