body.modal--in.no-scroll {
    position: fixed;
    width: 100%;

    .modal__background {
        opacity: 0.7;
    }
}

.modal__background {
    background: $color-nearblack;
    pointer-events: none;
    height: 100%;
    opacity: 0;
    top: 0;
    left: 0;
    position: fixed;
    transition: opacity 0.35s ease;
    width: 100%;
    z-index: z-index('veil');
}

.modal__popup {
    align-items: center;
    display: flex;
    height: 100%;
    justify-content: center;
    left: 0;
    opacity: 1;
    pointer-events: auto;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: z-index('modal');
}

.modal__popup[data-hidden='true'] {
    opacity: 0;
    pointer-events: none;
}

.modal__content {
    background: $color-purewhite;
    border: none;
    max-height: 80vh;
    overflow-y: auto;
    padding: $measure--half $measure--medium1 $measure--medium3;
    position: relative;
}

.modal__heading {
    @include standfirst;
    font-size: $font-size--gamma;
    font-weight: $font-weight-medium;
}

a.modal__close {
    @include anchor-reset-properties;
    background-image: none;
    background-size: initial;
    color: $color-gray7;
    cursor: pointer;
    padding: $measure--threequarters;
    position: absolute;
    right: 1px;
    top: 1px;

    @include anchor-default-outline($color-outline);

    &:visited {
        color: $color-gray7;
    }

    &:hover {
        @include media('hover') {
            background-image: none;
            border: none;
            background-color: $color-gray1;
            color: $color-gray10;
        }

        &:before {
            font-size: $font-size--icon + 0.2;
        }
    }

    &:active {
        background-color: $color-gray1;
        color: $color-gray10;
        @include anchor-border-reset;
    }

    &:focus {
        color: $color-gray10;
        @include anchor-border-reset;
    }

    &:active,
    &:focus {
        outline-offset: 0;
    }
}

.modal__body-copy {
    margin-top: $measure--basic;
}

.modal__buttons {
    @include media('>tablet') {
        display: grid;
        grid-template-columns: auto auto;
        grid-column-gap: $measure--basic;
    }

    a {
        @include anchor-reset-properties;
        @include anchor-default-outline($color-outline);
        border: $border-width-hairline $border-style-solid $color-gray3;
        border-bottom-color: $color-gray3;
        border-bottom-width: $border-width-hairline;
        color: $color-gray8;
        display: block;
        font-size: $font-size--alpha;
        margin-top: $measure--small4;
        padding: $measure--threequarters;
        position: relative;

        &:visited {
            border-bottom-color: $color-gray3;
            color: $color-gray8;
        }

        &:hover {
            @include media('hover') {
                @include anchor-reset-properties;
                color: $color-purewhite;
                border: $border-width-hairline $border-style-solid $color-pink90;
                border-bottom-color: $color-pink90;
                border-bottom-width: $border-width-hairline;
                background-color: $color-pink90;
                background-image: none;
                background-size: initial;
            }
        }

        &:focus {
            background-color: $color-gray1;
            border: $border-width-hairline $border-style-solid $color-gray4;
            border-bottom-color: $color-gray4;
            border-bottom-width: $border-width-hairline;
            color: $color-gray11;
        }

        &:active {
            background-color: $color-pink100;
            border: $border-width-hairline $border-style-solid $color-pink100;
            border-bottom-color: $color-pink100;
            border-bottom-width: $border-width-hairline;
            color: $color-purewhite;
        }
    }
}

.no-click {
    pointer-events: none;
}

.no-js {
    .modal__popup {
        position: initial;
    }

    .modal__popup--hidden {
        display: block;
        opacity: 1;
    }

    .modal__content {
        max-height: none;
        opacity: 1;
        padding: 0;
        pointer-events: auto;
    }
}
