.course-info,
.course--promo-cards,
.course--ug__student-testimonials {
    @include standard-grid-wrapper();
}

.course--ug__student-testimonials {
    h2 {
        grid-column: 1/-2;
    }
    .unibuddy-card {
        grid-column: 11/17;
    }
}

.course-info__year {
    grid-column: 3/17;
    display: flex;
    justify-content: space-between;

    @include media('<=mobile') {
        grid-column: 1/-1;
        display: block;
    }

    @include media('>mobile', '<=between') {
        grid-column: 1/13;
    }

    .start-date {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        @include gammatype;

        @include media('<=between') {
            text-align: center;
            margin-bottom: $measure--basic;
        }
    }
}

.course--promo-cards__ranking {
    border: solid $border-width-default $color-red50;
    padding: $measure--basic;
    margin-top: $measure--medium3;
    background-color: $color-red5;

    h3 {
        margin-top: 0;
        @include gammatype;
    }

    p {
        @include alphatype;
    }
}

.course--promo-cards__ranking--first {
    grid-column: 3/10;
}

.course--promo-cards__ranking--sec {
    grid-column: 10/17;
}

.course-info__cta {
    a:first-child {
        margin-bottom: $measure--basic;
    }
}

.course--promo-cards__ranking--first,
.course--promo-cards__ranking--sec {
    @include media('<=mobile') {
        grid-column: 1/-1;
    }

    @include media('>mobile', '<=between') {
        grid-column: 1/13;
    }
}

.course--ug__keyinfo__intro,
.course--ug__keyinfo__details,
.course--ug__keyinfo__accordion {
    background: linear-gradient(
        360deg,
        rgba(255, 255, 255, 0.95) 0%,
        rgba(255, 255, 255, 0.95) 100%
    );
    padding: $measure--small3 $measure--small3;

    @include media('<=mobile') {
        grid-column: 1 / -1;
    }

    @include media('>mobile') {
        grid-column: 2 / 12;
    }

    @include media('>between') {
        grid-column: 3 / 17;
    }
}
.course--ug__keyinfo__intro {
    margin-top: $measure--small3;
}

.course--ug__keyinfo__details {
    margin-top: $measure--small3;
    display: flex;
    flex-wrap: wrap;

    &--column {
        flex: 1;

        @include media('<between') {
            flex-basis: 50%;

            &:nth-child(3) {
                margin-top: $measure--small3;
            }
        }

        @include media('<tablet') {
            flex-basis: 100%;

            &:nth-child(2) {
                margin-top: $measure--small3;
            }
        }
    }

    .label {
        display: block;
        @include font-family('heading');
        color: $color-gray7;
        font-weight: $font-weight-normal;
        margin-bottom: $measure--half;
    }

    span:not(.label) {
        display: block;
        color: $color-gray9;
        font-weight: $font-weight-book;
        @include font-family('default');
        margin-bottom: $measure--quarter;
    }
}

.course--ug__keyinfo__details__ucas-code {
    margin-top: $measure--small3;
}

.course--ug__keyinfo__details__location {
    margin-top: $measure--small3;
}

.parallax {
    clip-path: inset(0% 0% 0% 0%);

    img {
        height: 100%;
        object-fit: cover;
        position: fixed;
        top: 0;
        width: 100%;
        z-index: z-index('below');
    }
}

.course--ug__keyinfo__accordion {
    border-top: $border-width-default $border-style-solid $color-gray3;
    margin-bottom: $measure--small3;

    .accordion {
        .accordion__heading {
            background: $color-gray1;

            button {
                color: $color-nearblack;
                font-weight: $font-weight-book;
                @include alphatype();
            }
        }
    }
}

.course--ug__related-courses__wrapper {
    background: linear-gradient(
        360deg,
        rgba(255, 255, 255, 1) 0%,
        rgba(242, 242, 242, 1) 100%
    );
}
.course--ug__related-courses {
    @include standard-box();
    padding-bottom: $measure--medium8;
    padding-top: $measure--medium8;

    .related-content {
        display: grid;
        margin-top: $measure--basic;
        grid-template-columns: auto auto;

        @include media('<between') {
            display: block;
        }
    }

    h2 {
        @include heading--centered($color-pink80);
    }
}

.course--ug__footer {
    @include standard-box();
    max-width: 80rem;
    padding-top: $measure--small2;
    padding-bottom: $measure--medium3;

    h2 {
        margin-bottom: $measure--small2;
    }

    .footer__contact--two-cards,
    .footer__contact--one-card {
        display: flex;

        @include media('<between') {
            flex-direction: column;
        }

        .related-content {
            flex-basis: 66%;
            display: flex;
            margin-right: $measure--small2;
            padding-top: $measure--small2;
            border-top: $border-width-hairline $border-style-solid $color-gray4;

            @include media('<between') {
                flex-basis: 100%;
                margin-right: 0;
            }

            @include media('<tablet') {
                display: inline-block;
            }

            li {
                flex: 1;
                &:not(:last-child) {
                    margin-right: $measure--small2;

                    @include media('<tablet') {
                        margin-right: 0;
                    }
                }
            }
        }
    }

    .footer__contact--one-card {
        .related-content {
            flex-basis: 33%;

            @include media('<between') {
                flex-basis: 100%;
            }
        }
    }

    .card--contact .card--contact__header {
        display: none;
    }

    .useful-links {
        flex-basis: 33%;
        margin-top: $measure--medium1;
    }
}

.course--ug__pathway-image-cards {
    display: grid;
    grid-column-gap: $measure--medium7;
    grid-row-gap: $measure--medium7;
    margin-top: $measure--small4;

    @include media('>=tablet') {
        grid-template-columns: auto auto;
    }
}

.course--ug {
    .course--ug__tabs__panel__inner {
        grid-column: 4 / 16;
    }
}

.js {
    .course--ug {
        .tabs {
            @include standard-box;
            background: $color-purewhite;
            margin-top: $measure--medium7;

            .tabs__panel {
                border-top: none;
                background: transparent;
            }

            .tabs__panel__heading {
                text-align: center;
            }

            .tabs__panel__body {
                margin-top: $measure--medium7;
            }
        }
    }

    .accordion > .accordion__heading {
        border-bottom-color: transparent;
    }
}

.course--ug__section-wrapper--grey {
    background-color: $color-gray1;
}

.course--ug__usp {
    grid-column: 3 / 17;

    .standfirst {
        @include epsilontype();
        color: $color-pink80;
    }
}

.course--ug__usp__cta,
.course--ug__usp__stats {
    background-color: $color-purewhite;
}

.course--ug__usp__video {
    @include media('>tablet') {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: $measure--basic;
    }
}

.course--ug__charts {
    margin-top: $measure--medium7;
    @include standard-box;

    @include media('>=tablet') {
        display: flex;
    }
}

.course--ug {
    .footer-cards {
        color: $color-purewhite;

        a {
            @include anchor-inline(
                $color-gray12,
                $color-gray12,
                $color-purewhite,
                $color-outline--dark
            );
        }

        h2 {
            @include full-width-heading($color-pink70);
        }
    }
}
