.pathway-image-cards {
    background-color: $color-gray2;
    padding: $measure--small4;

    h2,
    h3 {
        text-align: center;
    }
}

.pathway-image-cards__blocks {
    display: grid;
    grid-column-gap: $measure--small4;
    grid-row-gap: $measure--medium7;
    margin-top: $measure--small4;

    @include media('>=tablet') {
        grid-template-columns: auto auto;
    }

    @include media('>desktop') {
        grid-template-columns: auto auto auto;
    }
}

.card--pathway-image {
    a {
        display: block;
        border: none;
        transition: none;
        color: $color-nearblack;
        position: relative;

        &:visited {
            color: $color-nearblack;
        }

        &:hover,
        &:active,
        &:focus {
            background-image: none;
            background-color: transparent;
            border: none;
            color: $color-nearblack;
            z-index: z-index('above');
        }

        &:focus,
        &:active {
            outline: none;

            &:after {
                @include outline-properties();
            }
        }

        @include thumbnail-gradient-scale;
        @include background-color-and-scale($color-gray1, 1.1);
        @include underline-transition--subelement(
            $color-gray7,
            $color-gray10,
            $border-width-thick,
            'underline-transition__title'
        );

        img {
            transition: transform $transition-duration ease-in;
            width: 100%;

            @include media('reducemotion') {
                transition: none;
            }
        }
    }

    .underline-transition__title {
        @include betatype();
    }
}

.card--pathway-image__icon {
    display: block;
    text-align: right;
    font-weight: $font-weight-normal;
}

.card--pathway-image__container {
    overflow: hidden;
    margin-bottom: $measure--basic;
}
