$chart-themes: (
    'purple': (
        $color-purple40,
        $color-purple60,
        $color-purple80,
    ),
    'red': (
        $color-red40,
        $color-red60,
        $color-red80,
    ),
    'pink': (
        $color-pink40,
        $color-pink60,
        $color-pink80,
    ),
);

@each $color-key, $color-values in $chart-themes {
    @each $color in $color-values {
        .chart--#{$color-key} {
            $i: index($color-values, $color);

            path:nth-child(#{$i}) {
                stroke: $color;
            }

            .chart__legend__item:nth-child(#{$i}) {
                .fa.fa-circle {
                    color: $color;
                }
            }
        }
    }
}

.chart {
    margin-bottom: $measure--small4;
    margin-right: $measure--small4;

    &:last-child {
        margin-right: 0;
    }

    @include media('>=tablet') {
        flex: 0 0 20%;
    }
}

.chart--piechart {
    margin-right: $measure--medium4;
}

.chart__legend {
    justify-content: center;
    display: flex;
    flex-direction: column;
}

.chart__legend__item__icon {
    font-size: $measure--small1;
    margin-right: $measure--threequarters;
}

.chart__legend {
    margin-top: $measure--small2;
}

.chart__title {
    margin-bottom: $measure--small2;
}

.chart__legend__item {
    margin-bottom: $measure--basic;
    align-items: center;
    display: flex;
    line-height: 1.5;
}

.chart--piechart__value {
    @include svg-path-animation;
}
